import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import { useMediaQuery } from "react-responsive";

import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";

import LaptopMacIcon from "@material-ui/icons/LaptopMac";

import PetsIcon from "@material-ui/icons/Pets";
import PowerIcon from "@material-ui/icons/Power";
import LocalCafeIcon from "@material-ui/icons/LocalCafe";

import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";

import oclocklogo from "../../img/K81NHwiA_400x400.jpg";
import stgervlogo from "../../img/logo combrailles.jpg";
import lthmlogo from "../../img/Logo-LTHM_900x900.jpg";
import ssiaplogo from "../../img/logo-formation-ssiap1-300x300.png";
import sstlogo from "../../img/Sauveteur_secouriste_du_travail_Logo.svg.png";

import SkillCardsXpMobile from "../CardsXpMobile/index.js";

import "./timelineformations.css";

const Desktop = ({ children }) => {
  const isDesktop = useMediaQuery({ minWidth: 992 });
  return isDesktop ? children : null;
};
const Tablet = ({ children }) => {
  const isTablet = useMediaQuery({ minWidth: 768, maxWidth: 991 });
  return isTablet ? children : null;
};
const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({ minWidth: 300, maxWidth: 767 });
  return isMobile ? children : null;
};
/*
const Default = ({ children }) => {
  const isNotMobile = useMediaQuery({ minWidth: 768 });
  return isNotMobile ? children : null;
};
*/
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: "20px 20px",
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

export default function CustomizedTimeline() {
  const classes = useStyles();

  return (
    <div>
      <Desktop>
        <Timeline align="alternate">
          <TimelineItem>
            <TimelineOppositeContent>
              <div className="cardsContain_white">
                De Octobre 2019  Mars 2020
              </div>
              <div className="bloc_logo_formations">
                <a
                  className="link_logo_formations"
                  href="https://oclock.io/formations/developpeur-web"
                >
                  <img className="logo_formations" src={oclocklogo} alt="Moi" />
                  O'clock
                </a>
              </div>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot variant="outlined">
                <LaptopMacIcon />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <div className="cardsContain">Dveloppeur Web</div>
                <Typography>
                   Oclock, cole de dveloppement web en tlprsentiel.
                  <br /> Titre Professionnel de Dveloppeur Web et Web Mobile,
                  niveau III (bac+2) (non prsent).
                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent>
              <div className="cardsContain_white">
                De Decembre 2013  Mai 2014
              </div>
              <div className="bloc_logo_formations">
                <a
                  className="link_logo_formations"
                  href="https://metiers-chien-animalerie.fr/formation-adulte/formations-longues/agent-cynophile-de-securite"
                >
                  <img className="logo_formations" src={stgervlogo} alt="Moi" />
                  ACS Lyce des Combrailles
                </a>
              </div>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot variant="outlined">
                <PetsIcon />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <div className="cardsContain">Agent Cynophile de Scurit</div>
                <Typography>
                  Au C.F.P.P.A de Blanquefort. Formations de Saint Gervais
                  d'Auvergne.
                  <br />
                  Agent Cynophile de Scurit, niveau V (BEP).
                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent>
              <div className="cardsContain_white">De 2005  2007</div>
              <div className="bloc_logo_formations">
                <a
                  className="link_logo_formations"
                  href="https://lycee-technique.gouv.mc/A-propos-de-l-etablissement/Sections"
                >
                  <img className="logo_formations" src={lthmlogo} alt="Moi" />
                  L.T.H.Monaco
                </a>
              </div>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot variant="outlined">
                <PowerIcon />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <div className="cardsContain">Electricien en btiment</div>
                <Typography>
                  {" "}
                  Au Lyce Technique et Htelier de Monaco.
                  <br />
                  Mtier de llectrotechnique, niveau V (BEP).
                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent>
              <div className="cardsContain_white">Valide jusqu'en 2021</div>
              <div className="bloc_logo_formations">
                <a
                  className="link_logo_formations"
                  href="https://www.pompiers.fr/grand-public/devenir-sapeur-pompier/metiers-surete-et-securite/diplome-service-de-securite-incendie"
                >
                  <img className="logo_formations" src={ssiaplogo} alt="Moi" />
                  SSIAP
                </a>
                <a
                  className="link_logo_formations"
                  href="http://www.inrs.fr/services/formation/publics/sauveteur-secouriste.html"
                >
                  <img className="logo_formations" src={sstlogo} alt="Moi" />
                  INRS - SST
                </a>
              </div>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot variant="outlined">
                <LocalCafeIcon />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <div className="cardsContain">
                  Agent de scurit incendie et sauveteur secouriste
                </div>
                <Typography>
                  Agent S.S.I.A.P 1 ( service de scurit incendie et
                  dassistance  personnes niveau 1 ) et S.S.T ( sauveteur
                  secouriste du travail )  ce jour recycls.
                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>
        </Timeline>
      </Desktop>
      <Tablet>
        <div className="SkillCards">
          <SkillCardsXpMobile />
        </div>
      </Tablet>
      <Mobile>
        <div className="SkillCards">
          <SkillCardsXpMobile />
        </div>
      </Mobile>
    </div>
  );
}
